import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { Link, useParams } from 'react-router-dom';

import styles from './HeaderAdmin.module.css';
import { setSortValue } from '../../store/slices/clientsSlice';
import { bodyScrollOverflowAuto, bodyScrollOverflowHidden } from '../../helper';

export const HeaderAdmin = () => {
  const dispatch = useDispatch();
  let { userId } = useParams();

  const [showSort, setShowSort] = useState(false);
  const sortValue = useSelector((state) => state.clients.sortValue);

  const setValue = (value) => {
    dispatch(setSortValue(value));
    setShowSort(false);
    bodyScrollOverflowAuto();
  };

  const showSortMenu = () => {
    setShowSort(!showSort);
    if (!showSort) {
      bodyScrollOverflowHidden();
    } else {
      bodyScrollOverflowAuto();
    }
  };

  return (
    <div className={styles.header}>
      <div className="container">
        <div className={styles.header_wrapper}>
          <Link to={'/smile'}></Link>

          {!userId ? (
            <button className={styles.filter_btn} onClick={() => showSortMenu()}>
              {sortValue.name}
            </button>
          ) : (
            ''
          )}
        </div>
      </div>
      {showSort ? (
        <div className={styles.sort_content_wrapper}>
          <div className={styles.sort_items_wrapper}>
            <div
              className={
                sortValue.filter === 'name'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() => setValue({ sort: 'name', filter: 'name', name: '  ' })}>
                
            </div>
            <div
              className={
                sortValue.filter === 'groupAccompanyIndividualAccompany'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() =>
                setValue({
                  sort: 'name',
                  filter: 'groupAccompanyIndividualAccompany',
                  name: '   ',
                })
              }>
                 
            </div>

            <div
              className={
                sortValue.filter === 'groupAccompany'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() => setValue({ sort: 'name', filter: 'groupAccompany', name: ' ' })}>
               
            </div>

            <div
              className={
                sortValue.filter === 'individualAccompany'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() =>
                setValue({
                  sort: 'name',
                  filter: 'individualAccompany',
                  name: ' ',
                })
              }>
               
            </div>
            <div
              className={
                sortValue.filter === 'calculated'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() => setValue({ sort: 'name', filter: 'calculated', name: '' })}>
              
            </div>
            <div
              className={
                sortValue.filter === 'noCalculated'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() =>
                setValue({
                  sort: 'name',
                  filter: 'noCalculated',
                  name: '    ',
                })
              }>
                  
            </div>
            <div
              className={
                sortValue.filter === 'noAccompany'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() =>
                setValue({ sort: 'name', filter: 'noAccompany', name: '   ' })
              }>
                
            </div>
            <div
              className={
                sortValue.filter === 'deteNew'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() =>
                setValue({ sort: 'newClient', filter: 'deteNew', name: '  ( )' })
              }>
                ( )
            </div>

            <div
              className={
                sortValue.filter === 'deteOld'
                  ? styles.sort_item + ' ' + styles.active
                  : styles.sort_item
              }
              onClick={() =>
                setValue({ sort: 'oldClient', filter: 'deteOld', name: '  ( )' })
              }>
                ( )
            </div>
          </div>
        </div>
      ) : (
        ''
      )}
    </div>
  );
};
